@import url('https://fonts.googleapis.com/css?family=Gugi|Raleway|Kanit');

html,
body,
h1,
p {
    margin: 0;
    cursor: default;
}

body {
    background: #151515;
    font-family: "Relative", mono, sans-serif;
}
::-webkit-scrollbar {
    display: none;
}
a {
    text-decoration: none;
}

*:focus {
    outline: none;
}

.container {
    overflow: hidden;
    padding-bottom: 100px;
}
.process{
    position: fixed;
    bottom:0;
    left:0;
    background-image: linear-gradient(139deg, #fb8817, #ff4b01, #c12127, #e02aff);
    opacity: 0.7;
    width: 0;
    height:4px;
    transition: 100ms linear;
    border-radius: 1px;
    z-index:9998;
}

// Banner Start
#Banner {
    background: linear-gradient(105deg, rgb(32, 25, 36) 75%, rgba(52, 48, 56, 0.6));
    height: 100vh;
    width: 100vw;
    position: relative;
}

.filter {
    background-image: url('/images/new_head.png');
    background-size: auto 90%;
    background-repeat: no-repeat;
    background-position: bottom -35px right -30px;
    position: absolute;
    width: 100vw;
    height: 100vh;

}

.head {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    position: absolute;
    background: rgba(0, 0, 0, 0.4)
}

.word {
    font-size: 110px;
    font-weight: bold;
    font-family: Kanit;
    color: #ECF5FF;
    letter-spacing: 1px;
}

.iconBox {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    transition: opacity 0.5s linear;

    img {
        width: 20px;
        height: 20px;
        margin: 20px;
        cursor: pointer;
        transition: 0.2s linear;
        &:hover{
            transform: translateY(-5px);
            cursor: pointer;
        }
    }
}

.mouse {
    width: 25px;
    height: 40px;
    border-radius: 40px;
    border: 2px solid #ECF5FF;
    position: absolute;
    bottom: 40px;
    left: calc(50% - 12.5px);
    display: flex;
    justify-content: center;
    cursor: pointer;
}

@keyframes BallMove {
    0% {
        transform: translateY(0);
        opacity: 1;
    }

    50% {
        transform: translateY(12.5px);
        opacity: 1;
    }

    100% {
        transform: translateY(25px);
        opacity: 0;
    }
}

.mouseBall {
    width: 4px;
    height: 7px;
    border-radius: 10px;
    background: #ECF5FF;
    animation: BallMove linear infinite 1s;
    margin-top: 5px;
}

@keyframes Line {
    from {
        opacity: 1;
    }

    to {
        opacity: 0;
    }
}

.wordLine {
    font-weight: 300;
}

// Banner end

// Introduction Start
.Introduction {
    width: 100%;
    background: linear-gradient(180deg, rgb(19, 15, 21) 50%, #151515);
    display: flex;
    justify-content: center;
}

#Introduction {
    width: 100%;
    padding: 70px 10%;
    box-sizing: border-box;
    position: relative;
    opacity: 0;
    transition: 1s linear;
    max-width: 1600px;
}

#Introduction::before {
    content: 'About';
    color: #0A0A0A;
    font-size: 9em;
    font-weight: 500;
    position: absolute;
    left: 10%;
}

.introductionTitle,
.introductionTitle h1,
.introductionRight,
.introductionRight h1 {
    background: url('/images/Introduction.jpg') no-repeat;
    background-position: center;
    background-size: 100%;
    background-attachment: fixed;
}

.introductionTitle h1 {
    z-index: 1;
    font-size: 4em;
    top: 160px;
    left: 17%;
    letter-spacing: 2px;
    position: absolute;
    cursor: default;
    color: transparent !important;
    -webkit-background-clip: text;
    font-weight: 100;
    font-family: Kanit;
}

.introductionBox {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    margin-top: 250px;
}

.introductionRight {
    width: 45%;
}

.introductionRight h1 {
    z-index: 1;
    font-size: 10em;
    right: 10%;
    position: absolute;
    cursor: default;
    color: transparent !important;
    -webkit-background-clip: text;
    font-weight: 100;
    font-family: Kanit;
}

.introductionLeft {
    width: 55%;
    color: #ECF5FF;
    font-size: 18px;
    line-height: 1.5;
    font-weight: 400;

    p {
        margin: 10px 20px;
    }
}

.introductionButton,
.introductionButton1 {
    background: #ECF5FF;
    color: #151515;
    border:1px solid #ECF5FF;
    padding: 10px 20px;
    width: fit-content;
    font-weight: 500;
    margin: 100px 0 3em 20px;
    font-family: Kanit;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s linear;
    opacity: 0.9;

    &:hover {
        transform: translateY(-4px);
    }
}
.introductionButton1 {
    background: #151515;
    color: #ECF5FF;
}
.buttonBox{
    display: flex;
    align-items: center;
}
.location{
    opacity: 0.8;
    margin:40px 30px 0 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    img{
        width: 30px;
        margin:0 10px;
    }
    div{
        background: url('/images/Introduction.jpg') no-repeat;
        background-position: center;
        background-size: 100%;
        background-attachment: fixed;
        font-weight: 700;
        z-index: 1;
        cursor: default;
        color: transparent !important;
        -webkit-background-clip: text;
        font-family: Kanit;
        letter-spacing: 1px;
        font-size: 18px;
    }
}


// Introduction End
