.cd-container {
    max-width: 1080px;
    margin: 0 auto;
    padding: 0 10%;
    border-radius: 2px;
}

.cd-container::after {
    content: '';
    display: table;
    clear: both;
}

#cd-timeline {
    position: relative;
    padding: 1em 0;
}

#cd-timeline::before {
    content: '';
    position: absolute;
    top: 0;
    left: 25px;
    height: 100%;
    width: 7px;
    background: #272727;
}

@media only screen and (min-width: 1170px) {

    #cd-timeline::before {
        left: 50%;
        margin-left: -3px;
    }
}

.cd-timeline-block {
    position: relative;
    margin: 2em 0;
}

.cd-timeline-block:after {
    content: "";
    display: table;
    clear: both;
}

.cd-timeline-block:first-child {
    margin-top: 0;
}

.cd-timeline-block:last-child {
    margin-bottom: 0;
}

@media only screen and (min-width: 1170px) {
    .cd-timeline-block {
        margin: 4em 0;
    }

    .cd-timeline-block:first-child {
        margin-top: 0;
    }

    .cd-timeline-block:last-child {
        margin-bottom: 0;
    }
}

.cd-timeline-img {
    position: absolute;
    top: 8px;
    left: 12px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    box-shadow: 0 0 0 6px #151515, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
}

.cd-timeline-img {
    background: url('/images/Experience.jpg') no-repeat;
    background-color: rgb(231, 178, 102);
    background-position: center;
    background-size: 100%;
    background-attachment: fixed;
}

@media only screen and (min-width: 1170px) {
    .cd-timeline-img {
        width: 30px;
        height: 30px;
        left: 50%;
        margin-left: -15px;
        margin-top: 15px;
        /* Force Hardware Acceleration in WebKit */
        -webkit-transform: translateZ(0);
        -webkit-backface-visibility: hidden;
    }
}

.timeline-content-info {
    color: rgba(255, 255, 255, 0.9);
    font-size: 14px;
    font-weight: 300;
}

.cd-timeline-content {
    position: relative;
    margin-left: 55px;
    margin-right: 10px;
    background: #272727;
    border-radius: 5px;
    padding: 1em;
    color: #ECF5FF;

    h2 {
        font-family: Kanit;
    }
}

.cd-timeline-content:after {
    content: "";
    display: table;
    clear: both;
}

.cd-timeline-content h2 {
    color: rgba(255, 255, 255, .9);
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 18px;
    font-weight: 400;
}

.cd-timeline-content p,
.cd-timeline-content .cd-date {
    color: rgba(255, 255, 255, .7);
    font-size: 13px;
    font-size: 0.8125rem;
}

.cd-timeline-content .cd-date {
    display: inline-block;
}

.cd-timeline-content p {
    margin-top: 1em;
    line-height: 1.6;
}

.cd-timeline-content::before {
    content: '';
    position: absolute;
    top: 16px;
    right: 100%;
    height: 0;
    width: 0;
    border: 7px solid transparent;
    border-right: 7px solid #272727;
    ;
}

@media only screen and (min-width: 768px) {
    .cd-timeline-content p {
        font-size: 14px;
        font-weight: 300;
        line-height: 22px;
    }

    .cd-timeline-content .cd-read-more,
    .cd-timeline-content .cd-date {
        font-size: 14px;
        font-size: 0.875rem;
    }

    .mobileMenu {
        display: none !important;
    }
}

@media only screen and (min-width: 1170px) {
    .cd-timeline-content {
        color: white;
        margin-left: 0;
        padding: 1.4em;
        width: 36%;
        margin: 0 5%
    }

    .cd-timeline-content::before {
        top: 24px;
        left: 100%;
        border-color: transparent;
        border-left-color: #272727;
        ;
    }

    .cd-timeline-content .cd-date {
        position: absolute;
        width: 100%;
        left: 122%;
        top: 6px;
        font-size: 16px;
        font-size: 1rem;
    }

    .cd-timeline-block:nth-child(even) .cd-timeline-content {
        float: right;
    }

    .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
        top: 24px;
        left: auto;
        right: 100%;
        border-color: transparent;
        border-right-color: #272727;
    }

    .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
        float: right;
    }

    .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
        left: auto;
        right: 122%;
        text-align: right;
    }
}

.frame {
    padding: 20px 2vw 10px 2vw;
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    z-index: 9999;
    animation: eMLfYp 800ms;
    transition: 300ms linear;
}

.frameSection {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    opacity: 0.6;
    transition: 0.3s linear;
}
%menu{
    margin: 0 15px;
    padding: 4px;
    cursor: pointer;
    letter-spacing: 4px;
    font-size: 12px;
    font-family: Kanit;
    -webkit-transition: all 300ms ease;
    transition: all 300ms ease;
    border-radius: 5px;
    text-decoration: none;
    position: relative;
}
.menu {
    color: #ECF5FF;
    &:after {
        background: none repeat scroll 0 0 transparent;
        bottom: 0;
        content: "";
        display: block;
        height: 1px;
        left: 50%;
        position: absolute;
        background: #EF7A97;
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
        width: 0;
    }
    &:hover {
        color:#EF7A97;
    }
    &:hover:after {
        width: 100%;
        left: 0;
    }
    @extend %menu
}

.menuB {
    color: #798DDD;
    font-weight: bold;
    &:after {
        background: none repeat scroll 0 0 transparent;
        bottom: 0;
        content: "";
        display: block;
        height: 1px;
        left: 50%;
        position: absolute;
        background: #798DDD;
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
        width: 0;
    }
    &:hover {
        color:#798DDD;
    }
    &:hover:after {
        width: 100%;
        left: 0;
    }
    @extend %menu
}

.menuR {
    &:after {
        background: none repeat scroll 0 0 transparent;
        bottom: 0;
        content: "";
        display: block;
        height: 1px;
        left: 50%;
        position: absolute;
        background: #EF7A97;
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
        width: 100%;
        left: 0;
    }
    color:#EF7A97;
    @extend %menu
}

.Mmenu {
    text-align: center;
    padding: 10px 0;
