        left: 4%
    }

    .introductionTitle,
    .introductionTitle h1,
    .introductionRight,
    .introductionRight h1,
    .experienceTitle,
    .experienceTitle h1,
    .skillsTitle,
    .skillsTitle h1,
    .projectTitle,
    .projectTitle h1,
    .cd-timeline-img,
    .skillsTitle .skillsSubtitle {
        background-attachment: unset;
    }


    .introductionTitle h1,
    .experienceTitle h1,
    .skillsTitle h1,
    .projectTitle h1 {
        font-size: 3em;
        top: 80px;
        left: 10%;
    }

    .introductionBox {
