        justify-content: flex-start;
        img{
            width: 20px;
            margin:0 8px;
        }
        div{
            font-size: 16px;
            background-attachment: unset;
            letter-spacing: 0;
        }
    }
    .introductionButton,
    .introductionButton1{
        margin: 8px 0;
        padding: 10px 15px;
    }
    .buttonBox{
        flex-direction: column;
        align-items: flex-start;
        margin:40px 0 0 10px;
    }
    .introductionRight h1 {
        font-size: 4em;
        right: 0;
        bottom: 20px;
    }

    .experienceBox {
        margin-top: 150px;
    }

    .cd-timeline-content h2 {
        font-size: 16px;
    }

    .skillsBox,
    .projectBox {
        margin-top: 150px;
        padding: 0;
    }
    .skillsTitle .skillsSubtitle {
        font-size: 1.6em;
