    }

    .UrlSection {
        border-radius: 7px 7px 0 0;
        padding: 7px 25px 5px 15px;
    }
    .Dot{
        width: 6px;
        height: 6px;
    }
    .desktopMenu {
        display: none;
    }

    .mobileMenu {
        display: none;
        overflow: hidden;
        height:calc(100vh - 70px);
        box-sizing: border-box;
    }

    .frame {
        padding: 10px 15px;
        width: 100%;
        background: rgba(0, 0, 0, 0);
        box-sizing: border-box;
    }

    .frameSection {
        padding-bottom: 10px;
    }

    .logo {
        margin: 0;
        height:40px;
    }

    .menu {
